.add-info-div {
  margin: 13px 0px 0px 12px;
  height: max-content;
  overflow: hidden;
}

.swipermobile {
  position: 'fixed';
  margin-left: '60px';
}

.country-flag {
  width: 25px !important;
  height: 25px !important;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
}

.card_image_div {
  height: 130px;
  border-radius: 7px;
  max-width: 35%;
  padding: 0px;
  transform: translate(15px, 15px);
  background-color: #ffffff;
  box-shadow: 0px 0px 10px 0px #9c9c9c;
}

.card_image {
  height: 100%;
  border-radius: 7px;
  width: 100%;
  object-fit: contain;
  object-position: center;
}

.whole-card,
.whole-card-back {
  background-color: transparent;
  max-width: 450px;
  width: 100%;
  min-height: 210px;
  max-height: 210px;
  margin: 17px 15px;
  overflow: hidden;
  backface-visibility: hidden;
}

.whole-card-back {
  position: absolute;
  top: 0;
  transform: rotateY(180deg);
}

.card-rotate {
  transform: rotateY(180deg);
  transition: transform 0.6s;
}
.card-back-rotate {
  transform: rotateY(360deg);
  transition: transform 0.6s;
}

.back-image {
  background-position: 150px;
  background-size: contain;
  background-repeat: no-repeat;
  height: 400px;
  width: 100%;
  background-color: white;
}
.back-image-dark {
  background-position: 150px;
  background-size: contain;
  background-repeat: no-repeat;
  height: 400px;
  width: 100%;
  background-color: #0e0917;
}

.back-image-revert {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 400px;
  width: 100%;
  background-color: transparent;
  background-color: white;
  filter: brightness(0.15);
}
.back-image-dark-revert {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 400px;
  width: 100%;
  background-color: transparent;
  background-color: #0e0917;
  filter: brightness(0.15);
}

.back-image-normal {
  transform: scale(1);
  transition: 0.2s linear;
  filter: brightness(1);
}

.back-image-scale {
  transform: scale(1.2);
  transition: 0.2s linear;
  filter: brightness(0.7);
}

.card-title-div {
  position: absolute;
  bottom: 25px;
  left: 20px;
  text-align: center;
  width: max-content;
  border-radius: 4px;
  transition: 0.2s linear;
}

.card_title {
  font-size: 1.3rem;
  font-weight: bolder;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.card-title-div-up {
  bottom: 85px;
  transition: 0.2s linear;
}

.description-div {
  position: absolute;
  bottom: -80px;
  background-color: white;
  height: 80px;
  transition: 0.2s linear;
  width: 100%;
}

.description-div-up {
  bottom: 0;
  transition: 0.2s linear;
}

.card_description {
  font-size: 1.1rem;
  font-weight: bold;
  padding: 15px 20px 0px;
}

.register-button {
  color: var(--warning) !important;
  background-color: transparent !important;
  border: 1px solid var(--warning) !important;
  padding: 0.375rem 0.75rem !important;
}

.register-button:hover {
  box-shadow: 0 8px 25px -8px var(--warning) !important;
  color: var(--warning) !important;
  background-color: transparent !important;
  border: 1px solid var(--warning) !important;
}

@keyframes feature-up-down {
  0% {
    height: 0px;
  }
  100% {
    height: 730px;
  }
}
@keyframes plan-up-down {
  0% {
    height: 0px;
  }
  100% {
    height: 150px;
  }
}
@keyframes why-up-down {
  0% {
    height: 0px;
  }
  100% {
    height: 150px;
  }
}

.feature-menu-option {
  background-color: white;
  height: max-content;
  margin: 20px auto 20px;
  width: 98%;
  /* box-shadow: 0px 4px 25px 0px rgb(0 0 0 / 10%); */
  border-radius: 0.5rem;
  position: relative;
  overflow: hidden;
  animation: feature-up-down 0.35s linear;
}

.plan-menu-option {
  background-color: white;
  height: max-content;
  margin: 20px auto 20px;
  width: 98%;
  box-shadow: 0px 4px 25px 0px rgb(0 0 0 / 10%);
  border-radius: 0.5rem;
  position: relative;
  overflow: hidden;
  animation: plan-up-down 0.35s linear;
}

.why-menu-option {
  height: 150px;
  margin: 20px auto 20px;
  width: 98%;
  /* box-shadow: 0px 4px 25px 0px rgb(0 0 0 / 10%); */
  border-radius: 0.5rem;
  position: relative;
  overflow: hidden;
  animation: why-up-down 0.35s linear;
}

@media (min-width: 1100px) and (max-width: 1310px) {
  .card_description {
    font-size: 0.9rem;
  }

  .card_title {
    font-size: 1.4rem;
  }
}

@media (max-width: 500px) {
  .spacing {
    display: none !important;
  }
  .swiper-pagination-bullets {
    bottom: 5px !important;
  }

  .mobile_content {
    max-height: calc(100vh - 4.8rem);
    min-height: calc(100vh - 4.8rem);
    overflow: hidden;
  }

  .menu_content {
    background-color: transparent;
    width: 100%;
    min-height: calc(100vh - 28rem);
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-rows: auto auto auto;
    column-gap: 0px;
    row-gap: 0px;
    text-align: center;
    padding: 10px;
  }

  .menu_options {
    display: block;
    height: 100%;
    width: 100%;
    padding: 1.3rem 0px 0px;
    border-top: 1px solid #ffb874;
    border-left: 1px solid #ffb874;
  }

  .menu_label {
    font-size: 1.1rem !important;
    font-weight: 500;
    letter-spacing: 1px;
    margin-top: 5px;
    width: 105px;
  }

  .menu_icon {
    height: 30px;
  }

  .swiper-container {
    height: 260px !important;
  }
  .main-menu {
    width: 85px !important;
    height: calc(100vh - 110px);
    margin-top: 4.5rem;
  }
  .swipermobile {
    display: none;
  }
  .navbar-container {
    padding-left: 0.5rem !important;
    padding-right: 0 !important;
  }
  .menu-swipe-area {
    z-index: 1000 !important;
  }

  .main-menu-content {
    height: 100% !important;
  }

  .copyright-div {
    display: none;
  }

  .side-options {
    background-color: transparent;
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 35px;
    column-gap: 0px;
    row-gap: 0px;
    text-align: center;
    place-items: center;
    border-top: 1px solid #ffb874;
    padding: 10px 2px;
  }

  .side_menu_label {
    font-size: 0.9rem;
  }
  .side_menu_label {
    font-size: 0.9rem;
    font-weight: bold;
    color: black !important;
  }

  .side_menu_icon {
    font-size: 38px;
    font-weight: bold;
    color: rgb(255, 125, 0);
  }

  .sidebar_open {
    position: absolute;
    height: 55px;
    width: 55px;
    border: 3px solid #a7a7a7;
    padding: 8px 5px 0px 7px;
    border-radius: 50%;
    transform: translate(-22px, -25px);
  }

  .country-flag-selector {
    width: 28px !important;
    height: 28px !important;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    top: 4px;
  }

  .country-flag {
    width: 30px !important;
    height: 30px !important;
    border-radius: 50%;
    object-fit: cover;
  }
}

@media (min-width: 360px) and (max-width: 400px) and (min-height: 640px) and (max-height: 660px) {
  .menu_icon {
    height: 25px;
  }

  .menu_label {
    font-size: 0.9rem !important;
    width: 90px;
  }

  .menu_options {
    padding: 12px 0px 12px;
  }

  .sidebar_open {
    transform: translate(-22px, -20px);
  }
}
@media (max-width: 500px) {
  .hiding {
    display: none !important;
  }
}
@media (max-width: 500px) and (min-height: 701px) and (max-height: 800px) {
  .menu_options {
    padding: 2.3rem 0px 0px;
  }
}

@media (max-width: 500px) and (min-height: 801px) and (max-height: 900px) {
  .menu_options {
    padding: 2.6rem 0px 0px;
  }
}

@media (max-width: 500px) and (min-height: 901px) and (max-height: 1000px) {
  .menu_options {
    padding: 4.6rem 0px 0px;
  }
}

@media (max-width: 500px) and (min-height: 1001px) and (max-height: 1100px) {
  .menu_options {
    padding: 5.6rem 0px 0px;
  }
}
